@-webkit-keyframes MainAreaSlide {
  0%   {-webkit-transform: translate(-50%, -10%);transform: translate(-50%, -10%);}
  25%  {-webkit-transform: translate(-50%, -80%);transform: translate(-50%, -80%);}
  50%  {-webkit-transform: translate(-50%, -120%);transform: translate(-50%, -120%);}
  100% {-webkit-transform: translate(-50%, -250%);transform: translate(-50%, -250%);}
}



@keyframes MainAreaSlide {
  0%   {-webkit-transform: translate(-50%, -10%);transform: translate(-50%, -10%);}
  25%  {-webkit-transform: translate(-50%, -80%);transform: translate(-50%, -80%);}
  50%  {-webkit-transform: translate(-50%, -120%);transform: translate(-50%, -120%);}
  100% {-webkit-transform: translate(-50%, -250%);transform: translate(-50%, -250%);}
}
@-webkit-keyframes Top {
  /* 0%   {top: 3em;} */
  /* 25%  {top: 2.5em;}
  50%  {top: 2.2em;} */
  100% {top: 2em;}
}
@keyframes Top {
  /* 0%   {top: 3em;} */
  /* 25%  {top: 2.5em;}
  50%  {top: 2.2em;} */
  100% {top: 2em;}
}

@-webkit-keyframes LogoSlide {
  0%   {-webkit-transform: translateX(-10%, -10%) scale(0.8, 0.8);transform: translateX(-10%, -10%) scale(0.8, 0.8);}
  /* 25%  {transform: translateY(-30%);} */
  /* 50%  {transform: translateY(-50%);}  */
  100% {-webkit-transform: translate(-150%, -60%) scale(0.5, 0.5);transform: translate(-150%, -60%) scale(0.5, 0.5);}
}

@keyframes LogoSlide {
  0%   {-webkit-transform: translateX(-10%, -10%) scale(0.8, 0.8);transform: translateX(-10%, -10%) scale(0.8, 0.8);}
  /* 25%  {transform: translateY(-30%);} */
  /* 50%  {transform: translateY(-50%);}  */
  100% {-webkit-transform: translate(-150%, -60%) scale(0.5, 0.5);transform: translate(-150%, -60%) scale(0.5, 0.5);}
}

