  /*.Entry:hover ~ */
.SResults_Appear{
    -webkit-animation: slideIN 1s linear forwards;
    animation: slideIN 1s linear forwards ;
}

@-webkit-keyframes slideIN {
    0%   {-webkit-transform: translateY(100%);transform: translateY(100%);}
    25%  {-webkit-transform: translateY(75%);transform: translateY(75%);}
    50%  {-webkit-transform: translateY(50%);transform: translateY(50%);}
    100% {-webkit-transform: translateY(5%);transform: translateY(5%);}
}

@keyframes slideIN {
    0%   {-webkit-transform: translateY(100%);transform: translateY(100%);}
    25%  {-webkit-transform: translateY(75%);transform: translateY(75%);}
    50%  {-webkit-transform: translateY(50%);transform: translateY(50%);}
    100% {-webkit-transform: translateY(5%);transform: translateY(5%);}
}

