@-webkit-keyframes FadeIn {
    0%   {-webkit-transform: translateY(-100%);transform: translateY(-100%); height: 2em;}
    25%  {-webkit-transform: translateY(-40%);transform: translateY(-40%); height: 3em;}
    50%  {-webkit-transform: translateY(-20%);transform: translateY(-20%); height: 4em;}
    100% {-webkit-transform: translateY(0%);transform: translateY(0%); height: 5em;}
}

@keyframes FadeIn {
    0%   {-webkit-transform: translateY(-100%);transform: translateY(-100%); height: 2em;}
    25%  {-webkit-transform: translateY(-40%);transform: translateY(-40%); height: 3em;}
    50%  {-webkit-transform: translateY(-20%);transform: translateY(-20%); height: 4em;}
    100% {-webkit-transform: translateY(0%);transform: translateY(0%); height: 5em;}
}
@-webkit-keyframes FadeOut {
    0%   {-webkit-transform: translateY(-20%);transform: translateY(-20%); height: 5em;}
    25%  {-webkit-transform: translateY(-40%);transform: translateY(-40%); height: 4em;}
    50%  {-webkit-transform: translateY(-80%);transform: translateY(-80%); height: 3em;}
    100% {-webkit-transform: translateY(-150%);transform: translateY(-150%); height: 2em;}
}
@keyframes FadeOut {
    0%   {-webkit-transform: translateY(-20%);transform: translateY(-20%); height: 5em;}
    25%  {-webkit-transform: translateY(-40%);transform: translateY(-40%); height: 4em;}
    50%  {-webkit-transform: translateY(-80%);transform: translateY(-80%); height: 3em;}
    100% {-webkit-transform: translateY(-150%);transform: translateY(-150%); height: 2em;}
}

