.display_tweets{
    width: 60%;
    /* max-height: 50%; */
    position:relative;
    left: 20%;
    /* transform: translate(50%, 0%); */
    /* border: 3px solid red; */
    margin: 3em 0;
    background-color: #14171aec;
    border-radius: 0.4em;
    overflow: hidden;
}
.display_btns{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    /* bottom: 50px; */
    /* overflow: hidden; */
    /* height: 50px; */
    /* border: 3px solid red; */
}

.display_btns button{
    width: 17em;
    height: 3em;
    /* border-radius: 0.6em; */
    /* border-end-end-radius: 0.6em; */
    margin: 2em 0;
    padding: 0;
    font-family: 'Roboto', 'BlinkMacSystemFont';
    font-size: 0.8em;
    background-color: rgba(248, 244, 244, 0.749);
    cursor: pointer;
}

.display_btns button:hover{
    cursor: pointer;
}

.positive_btn{
    border-bottom-left-radius: 0.6em;
    border-top-left-radius: 0.6em;
    border-right: 1px solid #657786;
}
.positive_btn:hover{
    background-color: #52b7f1d0;
    border: 1px solid #ccc; 
    box-shadow: -2px 4px #52b7f19f;
}
.positive_btn[clicked=true]{
    background-color: #52b7f1;
    border: 1px solid #ccc; 
    box-shadow: -2px 1px #52b7f19f;
}
.negative_btn{
    border-bottom-right-radius: 0.6em;
    border-top-right-radius: 0.6em;
}.negative_btn:hover{
    background-color: #f15264de;
    border: 1px solid #ccc; 
    box-shadow: 2px 4px #f1526498;;
}
.negative_btn[clicked=true]{
    background-color: #f15264;
    border: 1px solid #ccc; 
    box-shadow: 1px 2px #f1526498;
}
.hide_buttons{
    /* opacity: 0; */
