@keyframes FadeIn {
    0%   {transform: translateY(-100%); height: 2em;}
    25%  {transform: translateY(-40%); height: 3em;}
    50%  {transform: translateY(-20%); height: 4em;}
    100% {transform: translateY(0%); height: 5em;}
}
@keyframes FadeOut {
    0%   {transform: translateY(-20%); height: 5em;}
    25%  {transform: translateY(-40%); height: 4em;}
    50%  {transform: translateY(-80%); height: 3em;}
    100% {transform: translateY(-150%); height: 2em;}
}

